.place-mark {
  position: relative;
  width: 45px;
  height: 45px;
  display: inline-block;
  background-color: #fff;
  border: 2px solid #FF1E1E;
  border-radius: 50%;

  &:hover {
    background-color: #fff;
    width: 45px;
    height: 45px;
    border: 4px solid #FF1E1E;

    &:after {
      content: '';
      position: absolute;
      z-index: 0;
      top: -56%;
      left: -56%;
      width: 45px;
      height: 45px;
      border: 17px solid rgba(255, 51, 51, 0.2);
      border-radius: 50%;
      box-sizing: content-box;
    }

    .place-mark__count {
      font-weight: 700;
      color: #FF1E1E;
    }
  }

  &__sale-icon {
    width: 25px;
    height: 25px;
    position: absolute;
    left: 8px;
    top: 8px;
    fill: #969696;
  }

  &:active {
    .place-mark__count {
      font-weight: 700;
      color: #FF1E1E;
    }
  }

  &.active {
    .place-mark__count {
      font-weight: 700;
      color: #FF1E1E;
    }
  }

  &__count {
    font-family: Arial;
    font-size: 18px;
    font-weight: normal;
    color: #969696;
    line-height: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%);
  }
}
